@import url(https://fonts.googleapis.com/css2?family=Righteous&family=Work+Sans&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#root{height:100%}

.App{height:100%}

.Home{background-color:#63c7ff;height:100%}

.LoadingWrapper{background:#63c7ff;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;text-align:center;height:100%;position:relative}.LoadingWrapper h1{margin-top:21px;color:#fff;font-size:26px;line-height:32.28px;letter-spacing:2px;font-family:'Righteous'}.LoadingWrapper .zocomIcon{position:absolute;bottom:44px;left:0;right:0}.LoadingWrapper .snowflake{-webkit-animation:3s hover ease-out alternate infinite;animation:3s hover ease-out alternate infinite;-webkit-perspective:600px;perspective:600px;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}@-webkit-keyframes hover{0%{-webkit-transform:translatey(-200px) rotatez(90deg) skewX(30deg) scale(0.6);transform:translatey(-200px) rotatez(90deg) skewX(30deg) scale(0.6)}100%{-webkit-transform:translatey(0px) rotatez(0deg) scale(1);transform:translatey(0px) rotatez(0deg) scale(1)}}@keyframes hover{0%{-webkit-transform:translatey(-200px) rotatez(90deg) skewX(30deg) scale(0.6);transform:translatey(-200px) rotatez(90deg) skewX(30deg) scale(0.6)}100%{-webkit-transform:translatey(0px) rotatez(0deg) scale(1);transform:translatey(0px) rotatez(0deg) scale(1)}}

.navBar{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.navBar .searchIcon{margin:10px;cursor:pointer;height:42px;width:42px}.navBar .navSnowflake{margin:10px}

.searchWrapper{background:#63c7ff;min-height:100vh;margin:0;padding:0;box-sizing:border-box;overflow:hidden}.searchWrapper .logo{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.searchWrapper .leftLogo{margin:10px}.searchWrapper .rightLogo{margin:10px}.searchWrapper .rightLogo:hover{cursor:pointer}.searchWrapper main{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;text-align:center;-webkit-flex-direction:column;flex-direction:column;width:100%;height:calc(100vh - 88px)}.searchWrapper .relative{position:relative;margin-top:40vh}.searchWrapper .inputCity{background:rgba(0,0,0,0.2);color:aliceblue;text-align:center;min-height:35px;width:300px;border-radius:16px;border-style:none;font-size:15px;max-width:288px;height:55px;transition:all 0.2s ease-in}.searchWrapper .inputCity:focus{-webkit-transform:scaleX(1.4);transform:scaleX(1.4);box-shadow:0px 0px 50px #c0fff5;outline:1px solid #15efff}.btn{position:relative;width:100%}button{padding:16px;border-radius:32px;border-style:none;background:#fff;font-size:18px;height:53px;max-width:307px;transition:all 0.2s ease-in;font-weight:bold;cursor:pointer;box-shadow:0 10px 20px -8px rgba(0,0,0,0.3);position:relative}button:hover{background:#00aeff;color:white;-webkit-transform:translateY(-2px);transform:translateY(-2px)}button:active{-webkit-transform:translateY(3px);transform:translateY(3px)}.lottie{pointer-events:none;width:100vw;height:100vh;position:absolute;bottom:0px}.clickHere{width:120px;position:absolute;top:0px;pointer-events:none}

.weatherWrapper{background:#63c7ff;min-height:100vh;color:#fff;padding:8px}.weatherWrapper h1{text-transform:capitalize}.weatherWrapper h2{font-size:24px}.weatherWrapper main{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;text-align:center;-webkit-flex-direction:column;flex-direction:column;-webkit-align-content:space-between;align-content:space-between}.weatherWrapper .weatherImage{margin-top:40px;-webkit-animation:weather 5s ease alternate infinite;animation:weather 5s ease alternate infinite;max-width:300px}.weatherWrapper p{color:#ffffff}.weatherWrapper .grader{color:white;font-size:144px;font-weight:300;text-align:center}.weatherWrapper .city{font-size:1.5rem}.weatherWrapper .time{font-size:1.4rem;margin-bottom:16px}.weatherWrapper table{display:block;font-size:18px;border-spacing:0;color:#ffffff;height:300px;max-width:479px;overflow-y:scroll;-ms-overflow-style:none;scrollbar-width:none}.weatherWrapper table ::-webkit-scrollbar{display:none}.weatherWrapper table .tableIcon{height:32px;width:32px}.weatherWrapper th,.weatherWrapper td{padding:8px;text-align:center}.weatherWrapper .sunTimes{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;width:300px;margin-bottom:16px}.weatherWrapper .sunTimes img{height:32px;fill:white}.weatherWrapper .sunTimes div{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}@-webkit-keyframes weather{0%{-webkit-transform:translateY(-5px);transform:translateY(-5px)}100%{-webkit-transform:translatey(5px);transform:translatey(5px)}}@keyframes weather{0%{-webkit-transform:translateY(-5px);transform:translateY(-5px)}100%{-webkit-transform:translatey(5px);transform:translatey(5px)}}

